:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 500ms;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.navBtn {
  z-index: 99;
  color: #fff;
  cursor: pointer;
  width: 70px;
  height: 70px;
  background-color: #fff;
  border: 1px solid #7198d3;
  border-radius: 15px;
  outline: none;
  display: none;
  position: fixed;
}

#showModal {
  display: block;
  top: 20px;
  left: 30px;
}

#myBtn {
  bottom: 20px;
  right: 30px;
}

#myBtnMap {
  bottom: 100px;
  right: 30px;
}

#myBtnMenu {
  bottom: 180px;
  right: 30px;
}

#myBtn:hover, #myBtnMenu:hover, #myBtnMap:hover {
  border: 2px solid #53709b;
  box-shadow: 0 1px 3px rgba(160, 160, 160, .2), 0 1px 1px rgba(173, 173, 173, .14), 0 2px 1px -1px rgba(175, 175, 175, .12);
}

@keyframes wipe-enter {
  0% {
    transform: scale(0, .025);
  }

  50% {
    transform: scale(1, .025);
  }
}

@keyframes wipe-out {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .myBtn-animation {
    animation: wipe-enter 1s;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .is-hidden {
    animation: wipe-out 1s;
  }
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.trophyBtn {
  z-index: 99;
  color: #fff;
  cursor: pointer;
  width: 70px;
  height: 70px;
  background-color: #fff;
  border: 1px solid #7198d3;
