.main__modal-wrapper {
  font-family: Montserrat, sans-serif;
  position: relative;
}

@keyframes X-round {
  0% {
    scale: 1;
  }

  50% {
    scale: 1.1;
  }
}

.close-modal-button {
  z-index: 111;
  width: 50px;
  height: 50px;
  cursor: pointer;
  background: #fff;
  border: 3px solid #12a0ff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
  display: flex;
  position: fixed;
  top: 6%;
  right: 8%;
  box-shadow: -2px 2px 4px 1px #33343e;
}

.close-modal-button:after {
  content: "";
  width: 43px;
  height: 43px;
  background-image: url("X.274af157.png");
  background-size: 43px;
  border-radius: 50%;
  rotate: 45deg;
}

.close-modal-button:hover {
  animation: X-round 2s linear infinite;
}

.close-modal-button:hover:after {
  content: "";
  width: 50px;
  height: 50px;
  background-image: url("X.a80c4ff0.gif");
  background-size: 50px;
  border-radius: 50%;
  rotate: 45deg;
}

.main__modal-backdrop {
  width: 100%;
  height: 100vh;
  z-index: 100;
  background-color: rgba(0, 0, 0, .7);
  position: fixed;
  top: 0;
  left: 0;
}

.main__modal-window {
  width: 80%;
  height: 80%;
  overflow-y: overlay;
  overflow-x: overlay;
  z-index: 104;
  box-sizing: border-box;
  -webkit-box-shadow: 2px 2px 2px 2px #5a5a63;
  -moz-box-shadow: 2px 2px 2px 2px #6f707c;
  background-color: #fff;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 10%;
  left: 10%;
  box-shadow: 2px 2px 2px 2px #55565f;
}

.main__modal-window-wrapper {
  width: 84%;
  height: 84%;
  z-index: 101;
  background: linear-gradient(#fdff9a, #ffda5f, #ffd857, #fff387);
  border: 2px solid #000;
  border-radius: 30px;
  display: flex;
  position: fixed;
  top: 8%;
  left: 8%;
  box-shadow: 3px 3px 5px 2px #33343e;
}

.main__modal-window::-webkit-scrollbar {
  display: none;
}

.is-hidden-modal {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  width: 0;
  height: 0;
}

.X-modal {
  z-index: 111;
  -webkit-box-shadow: 1px 2px 4px 1px gray;
  -moz-box-shadow: 2px 2px 4px 1px #4d4d4d;
  cursor: pointer;
  background: #fff;
  border: 3px solid #12a0ff;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 20px;
  font-family: Braah One, sans-serif;
  font-size: 20px;
  line-height: 18px;
  transition: all 1s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 1px 2px 4px 1px #575757;
}

.X-modal:after {
  content: "";
  width: 50px;
  height: 50px;
  background-image: url("verified.0d8b5ec2.png");
  background-size: 50px;
  border-radius: 50%;
  transition: all 1.3s;
  position: absolute;
  top: 5px;
  transform: translateY(130%);
}

.X-modal:hover {
  color: #fff;
  background-color: #f0f8ff;
  border-color: #2fbdff;
  transform: scale(1.01);
}

.X-modal:hover:after {
  transform: translateY(0);
}

.modal__content-wrapper {
  justify-content: center;
  display: flex;
}

.modal__day-list {
  background: linear-gradient(#9af0ff, #71aaff, #57c1ff, #87ddff) center / cover;
  border: 1px solid #c5c5c5;
  border-radius: 20px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding: 20px;
  list-style-type: none;
  list-style-position: inside;
  display: flex;
  box-shadow: 1px 2px 3px 1px #33343e;
}

.modal__day__list-element {
  background-color: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  display: flex;
  box-shadow: 1px 2px 3px 1px #33343e;
}

.modal__list-title {
  font-size: 18px;
}

.modal__list {
  padding-left: 0;
  list-style-type: none;
  list-style-position: inside;
}

.modal__list-element {
  width: 160px;
  background-color: #fff;
  border-bottom: 1px solid #d6d6d6;
  justify-content: center;
  align-items: center;
  margin: 5px 0;
  padding: 5px;
  font-size: 12px;
  display: flex;
  position: relative;
}

.modal__list-element:hover {
  cursor: pointer;
  color: #222;
  background-color: #f3f3f3;
  border-radius: 5px;
}

.modal__list-element:last-child {
  border: none;
}

.checked {
